<template>
  <div class="about">
    <div class="ban" >
      <h3  @click="books()" class="now" ref="books">书架</h3>
      <h3 @click="set()" ref="set">设置</h3>
    </div>
    <div class="books" v-if="bool">
        <div class="bimg"  v-if="bkb">
            <img src="../../public/img/icons/tag.png" alt="">
            <p>汝还不将书架填满</p>
            <router-link to="/ph" tag="span">添加书籍</router-link>
        </div>
       <ul>
         <li v-for="(item,i) in bk" :key="i">
           <div class="left">
             <img :src="item.imgs" :alt="item.tit">
           </div>
           <div class="right">
             <h3>{{item.tit}}</h3>
             <p>作者：{{item.author}}</p>
             <p>阅读至第{{item.hua+1}}话</p>
             <p class="set">
               <span @touchstart="shif(i)">删除</span>
               <router-link tag="span" :to="{path:'/read',query:{'postdata':{hua:item.hua,id:item.id,num:item.num,urls:item.urls,inde:item.inde,max:item.max}}}">继续</router-link>
             </p> 
           </div>
         </li>
       </ul>
    </div>
    <div class="reds" v-else>
      <p @click="clear()">清除收藏</p>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      bool:true,
      bk:[],
      bkb:true
    }
  },
  methods:{
    books(){
      this.$refs.books.className="now";
      this.$refs.set.className="";
      this.bool=true
    },
    shif(i){
      if(confirm("确认删除该书籍？")){
        var arr = localStorage.collection
        arr=JSON.parse(arr)
        arr.splice(i,1)
        arr=JSON.stringify(arr)
        this.bk.splice(i,1)
        localStorage.collection=arr
      }
    },
    set(){
      this.$refs.set.className="now";
      this.$refs.books.className="";
      this.bool= false
    },
    clear(){
      localStorage.collection="";
      alert("书架以清空")
    }
  },
  created(){
    var obj = localStorage.collection
    if(typeof(obj)!="undefined"&&obj.length>2){
      obj = JSON.parse(obj)
      this.bk = obj
      this.bkb=false
      sessionStorage.current="0"
    }
  }
}
</script>
